'use client';

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { useState } from "react";
import { useToast } from "@/hooks/use-toast";

export default function SettingsPage() {
  const [apiKey, setApiKey] = useState('');
  const { toast } = useToast();

  const handleSave = () => {
    // In a real app, you'd want to save this to a secure backend or a local, persistent store.
    // For this prototype, we'll just show a confirmation toast.
    console.log("API Key saved (simulated):", apiKey);
    toast({
      title: "API Key Saved",
      description: "Your Gemini API key has been saved locally.",
    });
  };

  const handleCreateKey = () => {
    window.open('https://aistudio.google.com/app/apikey', '_blank');
  };

  return (
    <div className="flex flex-col gap-8">
      <div className="text-center">
        <h1 className="font-headline text-4xl font-bold tracking-tight lg:text-5xl">
          Settings
        </h1>
        <p className="mt-2 text-lg text-muted-foreground">
          Configure your Type Ace experience.
        </p>
      </div>

      <div className="flex justify-center">
        <Card className="w-full max-w-2xl">
          <CardHeader>
            <CardTitle className="font-headline">Gemini API Key</CardTitle>
            <CardDescription>
              To use the AI-powered features, you need to provide your own Gemini API key. Your key is stored locally and is not shared.
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="api-key">Your API Key</Label>
              <Input 
                id="api-key" 
                type="password" 
                placeholder="Enter your Gemini API key"
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
              />
            </div>
            <div className="flex flex-col sm:flex-row gap-2">
                 <Button onClick={handleCreateKey} variant="outline" className="w-full sm:w-auto">
                    Create API Key
                 </Button>
                 <Button onClick={handleSave} className="w-full sm:w-auto flex-grow">Save Key</Button>
            </div>
             <p className="text-xs text-muted-foreground pt-4">
                You can create a new Gemini API key for free at Google AI Studio.
             </p>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
